.contact-div {
    padding: 10% 13% 3% 13% !important;
}

.contact-img-div {
    padding: 3% 7%;
}

.contact-form-div {
    padding: 5.4% 7%;
}

#contact-img {
    width: 100%;
    height: auto;
}


.contact-form-div button {
    width: 100%;
    background-color: #0466c8;

    color: white;
    font-size: 1.2rem;
    font-weight: bold;

    margin: 5.4% 0;
}

textarea {
    border: 2px solid #0466c8;
    border-radius: 6px;
    padding: 1% 2.5%;
    width: 100%;

    margin: 3% 0;
}

/* ====================== for tablet version user interface ================= */
@media (min-width :768px) and (max-width: 992px) {

    .contact-div {
        padding: 12% 9% 3% 9% !important;
    }

    .contact-form-div {
        padding: 5.4% 4%;
    }
    
    .contact-img-div {
        padding: 3% 4%;
    }

}

/* ====================== for mobile version user interface ================= */
@media (min-width :320px) and (max-width: 425px) {

    .contact-div {
        padding: 17% 6% 3% 6% !important;
    }

    .contact-img-div {
        padding: 3% 8%;
    }

    .contact-form-div button {
        font-size: 1rem;
    }

}